import Vue from 'vue'
import co from 'co'

// post
let postGen = function * (method, url, data, {warnMsg, resolveMsg, rejectMsg}) {
  // 
  let msgRet = 'confirm'
  if (warnMsg) {
    msgRet = yield Vue.showMessage(warnMsg, ['confirm', 'cancel'])
  }
  if (msgRet === 'confirm') {
    try {
      let dataRet = yield HttpStore.http(method, url, data)
      if (resolveMsg) {
        // 
        yield Vue.showMessage(resolveMsg)
      }
      return dataRet
    } catch (error) {
      if (rejectMsg) {
        // 
        yield Vue.showMessage(`${rejectMsg}${error.status}${error.statusText}`)
      }
      throw error
    }
  } else if (msgRet === 'cancel') {
    // 
    let error = {status: 301, statusText: ''}
    throw error
  }
}

let HttpStore = {
  // http
  count: 0,
  // http
  https: [],

  install (Vue, options) {
    // Vuepost
    Vue.post = Vue.prototype.$post = function (url, data,
    {
      warnMsg = null,
      resolveMsg = '',
      rejectMsg = ''
    } = {}) {
      console.log('post', url)
      let gen = postGen('POST', url, data, {warnMsg, resolveMsg, rejectMsg})
      return co(gen)
    }

    // Vueremove
    Vue.remove = Vue.prototype.$remove = function (url, data,
    {
      warnMsg = '',
      resolveMsg = '',
      rejectMsg = ''
    } = {}) {
      console.log('delete', url)
      let gen = postGen('DELETE', `${url}/${data.id}`, {}, {warnMsg, resolveMsg, rejectMsg})
      return co(gen)
    }
  },

  // 
  httpEnd () {
    this.count--

    // 
    if (this.https.length > 0) {
      let send = this.https.splice(0, 1)[0]
      this.http(send.method, send.url, send.body).then(
        (response) => send.resolve(response)
      ).catch(
        (response) => send.reject(response)
      )
    }

    // busy
    if (this.count === 0) {
      this.isBusy = false
    }
  },

  // http
  http (method, url, body) {
    return new Promise((resolve, reject) => {
      if (this.isBusy === false) {
        this.isBusy = true
      }

      // 
      if (this.count < 5) {
        this.count++
        // body
        if (!body) {
          body = {}
        }

        Vue.http({
          url: url,
          data: JSON.stringify(body),
          method: method
        }).then((response) => {
          // 
          this.httpEnd()
          resolve(response)
        }).catch((response) => {
          // 
          this.httpEnd()
          reject(response)
        })
      } else {
        // 
        console.log(`put: ${JSON.stringify(body)}`)
        this.https.push({method, url, body, resolve, reject})
      }
    })
  }
}

// HttpStore
Vue.set(HttpStore, 'isBusy', false)

export default HttpStore



