import Vue from 'vue'

export default class TreeList {
    // , data
  static toTreeNode (rows, parent = null, level = 0) {
    return Array.from(rows, (row) => {
      let newRow = {
        data: row,      // data
        loaded: false,  // 
        parent: parent, // 
        level: level,   // 
        size: row.size, // 
        id: row.id      // id
      }
      // 
      if (!newRow.children) {
        newRow.children = []
      }
      // false
      if (newRow.open === undefined) {
        Vue.set(newRow, 'open', false)
      }
      // 
      Vue.set(newRow, 'state', '')
      return newRow
    })
  }

  constructor (url) {
    this.url = url
    this.rows = []
    this.state = ''
    // 
    this.error = ', '
  }

  // 
  // condition - 
  // model - 
  search (condition, model) {
    // 
    this.condition = condition
    this.model = model

    let params = Object.assign({}, model, {
      condition: condition
    })

    // 
    return Vue.post(this.url, params, {resolveMsg: null, rejectMsg: null}).then(
      (response) => {
        if (response.data.length === 0) {
          this.state = ''
          this.error = ''
          return
        }
        this.state = ''
        // 
        this.rows = response.data
        this.rows = TreeList.toTreeNode(this.rows)
      }
    ).catch(
      () => {
        this.state = ''
        this.error = ''
      }
    )
  }

  // 
  refresh () {
    return this.search(this.condition, this.model)
  }
}



