import Vue from 'vue'
import TreeList from '../stores/TreeList'
import co from 'co'

// 
function close (array, data) {
  // 
  array.$remove(data)
  if (!data.children) {
    return
  }

  // 
  for (let child of data.children) {
    close(array, child)
  }
}

// 
function open (array, data, index) {
  array.splice(index + 1, 0, data)

  // data
  if (!data.open || !data.children) {
    return
  }

  // data
  index = array.indexOf(data)
  for (let child of data.children) {
    open(array, child, index)
    index++
  }
}

// data
function proc (model, data) {
  // , 
  if (data.open) {
    let index = model.indexOf(data)
    for (let child of data.children) {
      open(model, child, index)
      index++
    }
  } else {
    for (let child of data.children) {
      // 
      close(model, child)
    }
  }
}

// togglegeneral
let toggleGen = function * (row, comp) {
  row.open = !row.open
  // 
  if (row.open && row.children.length === 0) {
    yield comp.loadChild(row)
    // 
    row.children = TreeList.toTreeNode(row.children, row, row.level + 1)
  }
  proc(comp.model, row)
}

export default {
  props: ['url', 'model'],
  created () {
    // 
    if (this.model && this.model.length > 0) {
      this.model = TreeList.toTreeNode(this.model)
    }
  },
  methods: {
    toggle (node) {
      let gen = toggleGen(node, this)
      return co(gen)
    },

    // 
    isFolder (data) {
      return data.size > 0
    },

    // 
    loadChild (node) {
      return new Promise((resolve, reject) => {
        if (node.loaded) {
          return
        }
        // 
        this.$post(
          this.url,
          {id: node.id},
          {resolveMsg: null}
        ).then((response) => {
          node.children = response.data
          node.loaded = true
          Vue.set(node, 'state', '')
          resolve()
        }).catch(() => {
          node.open = false
          Vue.set(node, 'state', '')
        })
      })
    },

    // 
    remove (url, node) {
      Vue.remove(url, node).then(() => {
        // model
        this.model.$remove(node)
        // 
        if (node.parent) {
          node.parent.children.$remove(node)
          // size
          node.parent.size = node.parent.children.length
        }
        // 
        if (this.select) {
          this.select(null)
        }
      })
    }
  }
}



