import Vue from 'vue'

export default class PagedList {
  // urlpageSize
  // url - url
  // pageSize - 
  // params - sql:{:}
  constructor (url, pageSize, params) {
    this.url = url
    this.pageSize = pageSize

    // 
    if (params) {
      this.paramSource = params
    }
    // 
    this.params = {}

    // 
    this.count = 0
    // 
    this.rows = []
    // ()
    this.state = ''
    // 
    this.error = ', '

    // 
    this.condition = null
    this.model = null
  }

  // 
  // condition - 
  // model - 
  search (condition, model) {
    // 
    this.condition = condition
    this.model = model

    this.state = ''
    this.procParams()

    // 
    Vue.post(`${this.url}/n`, this.params, {resolveMsg: null, rejectMsg: null}).then(
      (response) => {
        if (response.data.n === 0) {
          this.state = ''
          this.error = ''
          return
        }
        this.state = ''
        this.count = response.data.n
        // 
        this.loadPage(1)
      }
    ).catch(
      () => {
        this.state = ''
        this.error = ''
      }
    )
  }

  // 
  refresh () {
    this.search(this.condition, this.model)
  }

  // 
  loadPage (pageNo) {
    this.state = ''

    Vue.post(
      `${this.url}?pageNo=${pageNo}&pageSize=${this.pageSize}`,
       this.params,
      {resolveMsg: null, rejectMsg: null}
    ).then(
      (response) => {
        this.state = ''
        this.rows = response.data
      }
    ).catch(
      (error) => {
        this.state = ''
        this.error = ''
        throw error
      }
    )
  }

  // condition
  procParams () {
    this.params.condition = this.condition
    if (!this.paramSource) {
      return
    }
    // 
    for (let name in this.paramSource) {
      /*eslint-disable */
      this.params[name] = eval(this.paramSource[name])
      /*eslint-enable */
    }
  }

  // 
  remove (url, data) {
    Vue.delete(url, data).then(
      () => {
        this.refresh()
      }
    )
  }
}



